@charset "UTF-8";
input[type=range] {
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  background: linear-gradient(to right, #22c55e 0%, #22c55e 25%, #facc15 25%, #facc15 50%, #fb923c 50%, #fb923c 75%, #dc2626 75%, #dc2626 100%);
  border-radius: 5px;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background-color: white;
  cursor: pointer;
  border-radius: 50%;
  border: 2px solid #000;
}

input[type=range]::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background-color: #4CAF50;
  cursor: pointer;
  border-radius: 50%;
  border: 2px solid #000;
}

/* From Uiverse.io by satyamchaudharydev */
.card {
  max-width: 350px;
  background-color: rgb(255, 255, 255);
  background: white;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 20px;
}

.small-text,
.title,
.desc {
  font-weight: 600;
}

.title,
.desc {
  margin: 8px 0;
}

.small-text {
  color: #488aec;
  font-size: 14px;
}

.title {
  color: #fff;
  font-size: 26px;
  line-height: 26px;
}

.desc {
  color: rgb(151, 153, 167);
  font-size: 13px;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}

.button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  padding: 6px;
  text-decoration: none;
}

.button:first-child {
  box-shadow: 0 4px 6px -1px rgba(72, 138, 236, 0.1921568627), 0 2px 4px -1px rgba(72, 138, 236, 0.0901960784);
  background-color: #488aec;
}

.button:last-child {
  box-shadow: 0 4px 6px -1px rgba(11, 22, 37, 0.1921568627), 0 2px 4px -1px rgba(14, 25, 43, 0.0901960784);
  background-color: #fff;
}

.icon {
  height: 25px;
  width: 25px;
}

.icon svg {
  width: 100%;
  height: 100%;
}

.icon svg:last-child {
  fill: #000;
}

.button-text {
  display: flex;
  flex-direction: column;
  margin-left: 8px;
}

.button-text span:first-child {
  font-size: 12px;
  font-weight: 600;
}

.google span:first-child {
  color: rgb(219, 206, 253);
}

.apple span:first-child {
  color: rgb(81, 87, 108);
}

.google span:last-child {
  color: #fff;
  font-weight: 800;
}

.apple span:last-child {
  color: #000;
  font-weight: 800;
}

.grayscale {
  filter: brightness(0.5);
  transition: filter 0.5s ease;
}

